<template>
  <div class="CustomerDataDetailsHistory">
    <HeaderNavigation />
    <div class="CustomerDataDetailsHistoryBox">
      <div
        class="CustomerDataDetailsHistoryBoxBg"
        :style="{
          backgroundImage: 'url(' + backgroundImage + ')',
          backgroundSize: 'cover',
          backgroundRepeat: 'round'
        }"
      >
        <div class="CustomerDataDetailsHistoryBoxTitle">
          <h1>{{ name }}·保養歷程</h1>
          <h2 @click="toInstallBasics()">回客户資料</h2>
        </div>
        <div class="box_wrap">
          <!-- <div class="list">
            <div class="t">原水水壓</div>
            <div class="b">
              <b>{{ imageList.WaterPressure }}</b>
              <small>kg/cm²</small>
            </div>
          </div> -->
          <div class="list">
            <div class="t">餘氯</div>
            <div class="b">
              <b>{{ imageList.Chlorine }}</b>
            </div>
          </div>
          <div class="list">
            <div class="t">原水TDS</div>
            <div class="b">
              <b>{{ imageList.TDS }}</b>
              <small>PPM</small>
            </div>
          </div>
          <div class="list">
            <div class="t">原水PH值</div>
            <div class="b">
              <b>{{ imageList.PH }}</b>
            </div>
          </div>
        </div>
      </div>
      <div class="CustomerDataDetailsHistoryBoxPadding">
        <div class="CustomerDataDetailsHistoryBoxHeader">
          <span>
            <img :src="imgUrl" alt="" />
          </span>
          <div>
            <h3>
              {{ name }}
              <em>更換日：{{ dataTime }}</em>
            </h3>
            <h5>銷售金額：{{ personalDetails.Charge }}</h5>
            <!-- <h4>單號：{{ userInfoList.IncidentNo }}</h4> -->
            <h4>地址：{{ personalDetails.Addr }}</h4>
            <h4>電話：{{ this.$route.query.CustPhone }}</h4>
          </div>
        </div>
        <div
          class="CustomerDataDetailsHistoryBoxContent"
          v-for="(item, index) in dataFormList"
          :key="index"
        >
          <img src="@/assets/image/InstallCourseB.png" alt="" />
          <div>
            <h5>淨水器濾心{{ item.Sequence }}號</h5>
            <h6
              v-for="(items, indexs) in item.FilterChangeHistoryList"
              :key="indexs"
            >
              <em>{{ items.Mfno }}</em>
              <i>{{ items.LastChangeDate }}</i>
              <strong>{{ items.Charge }}</strong>
            </h6>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import {
  POST_GetFilterChangeHistoryForDistribution,
  POST_GetCustWaterProdList,
  POST_GetInstallData
} from "@/utils/api";
import HeaderNavigation from "@/components/HeaderNavigation";
export default {
  name: "CustomerDataDetailsHistory",
  components: {
    HeaderNavigation
  },
  data() {
    return {
      imageList: "",
      userInfoList: {},
      dataFormList: [],
      personalDetails: [],
      imgUrl: "",
      name: "",
      backgroundImage: require("@/assets/image/jpgbg.jpg"),
      dataTime: ""
    };
  },
  created() {
    this.GetFilterChangeHistoryForDistribution();
    this.GetCustomerByDistributtion();
    this.GetInstallData();
  },
  methods: {
    GetInstallData() {
      var form = {
        Mfno: this.$route.query.Mfno
      };
      // 获取安装图片
      POST_GetInstallData(form).then(response => {
        this.imageList = response.Data;
      });
    },
    // 获取客户信息
    GetCustomerByDistributtion() {
      var UofIds = {
        CustId: this.$route.query.id,
        DistributionUofId: sessionStorage.getItem("DistributionUofId")
      };
      POST_GetCustWaterProdList(UofIds).then(response => {
        response.Data.forEach(item => {
          if (this.$route.query.CustId == item.CustProdId) {
            this.personalDetails = item;
          }
        });
      });
    },
    // 获取保养记录
    GetFilterChangeHistoryForDistribution() {
      var ProductId = {
        ProductId: this.$route.query.CustId
      };
      POST_GetFilterChangeHistoryForDistribution(ProductId).then(response => {
        this.dataFormList = response.Data;
        var time = this.dataFormList[0].InstallDate.split("-");
        this.imgUrl = this.dataFormList[0].ModelPicture;
        this.name = this.dataFormList[0].ModelName;
        this.dataTime = time[0] + "年" + time[1] + "月" + time[2] + "日";
      });
    },
    toInstallBasics() {
      this.$router.go(-1);
    }
  }
};
</script>

<style lang="scss" scoped>
.CustomerDataDetailsHistory {
  width: 100%;
  .CustomerDataDetailsHistoryBox {
    padding: 0px 0 0;
    .CustomerDataDetailsHistoryBoxPadding {
      padding: 0 5.33%;
      margin-top: -50px;
    }
    .CustomerDataDetailsHistoryBoxBg {
      background-color: #1c76fe;
      padding: 22px 5.33%;
      min-height: 280px;
      .box_wrap {
        display: flex;
        flex-wrap: wrap;
        margin-top: 30px;
        .list {
          flex: 0 0 50%;
          margin-bottom: 14px;
          .t {
            font-size: 12px;
            font-weight: 400;
            color: rgba(255, 255, 255, 1);
            line-height: 17px;
            margin-bottom: 6px;
          }
          .b {
            b {
              font-size: 28px;
              color: rgba(255, 255, 255, 1);
              line-height: 32px;
            }
            small {
              font-size: 12px;
              font-weight: 400;
              color: rgba(255, 255, 255, 1);
              line-height: 17px;
              margin-left: 18px;
            }
          }
        }
      }
    }
    .CustomerDataDetailsHistoryBoxTitle {
      display: flex;
      justify-content: space-between;
      h1 {
        font-size: 18px;
        font-weight: 500;
        color: #ffffff;
        line-height: 25px;
      }
      h2 {
        font-size: 15px;
        font-weight: 500;
        color: #ffffff;
        line-height: 21px;
        text-decoration: underline;
      }
    }
    .CustomerDataDetailsHistoryBoxHeader {
      margin-top: 10px;
      margin-bottom: 14px;
      padding: 10px 2.67%;
      background: rgba(255, 255, 255, 1);
      border-radius: 8px;
      display: flex;
      align-items: center;
      span {
        display: inline-block;
        width: 58px;
        height: 58px;
        margin-right: 20px;
        text-align: center;
        img {
          max-width: 58px;
          max-height: 58px;
        }
      }
      div {
        h3 {
          font-size: 13px;
          font-weight: 500;
          color: rgba(61, 61, 61, 1);
          line-height: 18px;
          margin-bottom: 10px;
          em {
            margin-left: 8px;
          }
        }
        h4 {
          font-size: 12px;
          font-weight: 500;
          color: rgba(134, 134, 134, 1);
          line-height: 17px;
        }
        h5 {
          font-size: 13px;
          font-weight: 500;
          color: #0792F0;
          line-height: 18px;
        }
      }
    }
    .CustomerDataDetailsHistoryBoxContent {
      margin-bottom: 10px;
      padding: 19px 5.67%;
      background: rgba(255, 255, 255, 1);
      border-radius: 8px;
      display: flex;
      img {
        width: 10px;
        height: 22px;
      }
      div {
        width: 100%;
        margin-left: 8px;
        h5 {
          font-size: 13px;
          font-weight: 500;
          color: rgba(61, 61, 61, 1);
          line-height: 18px;
          margin-bottom: 7px;
        }
        h6 {
          width: 100%;
          font-size: 12px;
          font-weight: 500;
          color: rgba(134, 134, 134, 1);
          line-height: 17px;
          em {
            display: inline-block;
            width: 50%;
          }
          i {
            display: inline-block;
            width: 30%;
          }
          strong {
            text-align: center;
            display: inline-block;
            width: 20%;
          }
        }
      }
    }
  }
}
</style>
